import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {RolesComponent} from '../roles/roles.component';
import {ViewRoleComponent} from '../roles/view-role/view-role.component';
import {UpdateRoleComponent} from '../roles/update-role/update-role.component';
import {ChangePermationsComponent} from '../roles/change-permations/change-permations.component';
import {DeleteRoleComponent} from '../roles/delete-role/delete-role.component';
import {PermeationsComponent} from './permeations.component';
import {ViewPermeationComponent} from './view-permeation/view-permeation.component';
import {UpdatePermeationComponent} from './update-permeation/update-permeation.component';
import {DeletePermationComponent} from './delete-permation/delete-permation.component';

const routes: Routes = [
  {path: '', component: PermeationsComponent},
  {path: 'add-permeation', component: ViewPermeationComponent},
  {path: 'delete-permeation', component: DeletePermationComponent},
  {path: 'update-permeation/:id', component: UpdatePermeationComponent},


];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PermeationsRoutingModule { }
