import { Component, OnInit } from '@angular/core';
import {IAppAdmin, IpermationDetail} from '../../../Models/app-admin';
import {AppAdminService} from '../../../Services/app-admin.service';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {ToastaService} from 'ngx-toasta';

@Component({
  selector: 'app-update-permeation',
  templateUrl: './update-permeation.component.html',
  styleUrls: ['./update-permeation.component.css']
})
export class UpdatePermeationComponent implements OnInit {

  errorMessage: string[];
  permissions: IpermationDetail;
  roleId: number;
  updatedData: IAppAdmin;
  permissionsId: any[];

  constructor(public _AppAdminService: AppAdminService, private route: ActivatedRoute, private toastyService: ToastaService, private router: Router) {
    this.route.params.subscribe((params: Params) => {
      this.roleId = +params.id;
      this.getRoleById(this.roleId);
    });
  }

  ngOnInit(): void {
    this.getPermassions();
  }

  updateAdminRole() {
    this._AppAdminService.updateRole.permissions = this.permissionsId;
    this._AppAdminService.updatePermission(this.roleId).subscribe(() => {

    }, (error) => {
      if (error.error) {
        this.errorMessage = error.error;

      }
    }, () => {
      console.log(this._AppAdminService.AddRole);
      // this.isLoadingMoodTeacherteachers = false;
      // this.displayClassDialogue = false;
      this.errorMessage = null;
      // this.getAllteachers();
      this._AppAdminService.AddRole = {} as IAppAdmin;
      this.toastyService.success({
        title: 'Members',
        msg: 'This Permission Has Been Created successfully!',
        showClose: true,
        timeout: 3000,
        theme: 'material'
      });
      this.router.navigate(['/admin-panel/permeations']);
    });
  }

  getPermassions() {
    this._AppAdminService.AddRole.permissions;
    this._AppAdminService.getAllPermissions().subscribe((res: { data }) => {
      this.permissions = res.data as IpermationDetail;
      console.log(this.permissions);
    });
  }

  getRoleById(id: number) {
    this._AppAdminService.getPermissionID(id).subscribe((res: { data }) => {
      this.updatedData = res.data as IAppAdmin;
      console.log(this.updatedData);
    }, error => {
    }, () => {
      // this.showQuestionData();
      // this.permissionsId = this.updatedData.permissions.map(b => b.id);

      this._AppAdminService.updateRole = this.updatedData;

      // console.log(  this.invoiceService.InvoiceUpdate.status);

    });
  }

}
