import { Component, OnInit } from '@angular/core';
import {IAppAdmin} from '../../Models/app-admin';
import {MatDialog} from '@angular/material/dialog';
import {AppAdminService} from '../../Services/app-admin.service';
import {ToastaService} from 'ngx-toasta';
import {DeleteRoleComponent} from '../roles/delete-role/delete-role.component';
import {DeletePermationComponent} from './delete-permation/delete-permation.component';

@Component({
  selector: 'app-permeations',
  templateUrl: './permeations.component.html',
  styleUrls: ['./permeations.component.css']
})
export class PermeationsComponent implements OnInit {

  cols: any;
  products: any;
  Permission: IAppAdmin[];
  rolesPermations: any[];
  display: boolean = false;
  // toastOption: ToastOptions = {
  //     title: 'Events',
  //     msg: 'This Event Has Been Deleted successfully!',
  //     showClose: true,
  //     timeout: 3000,
  //     theme: 'material'
  // };

  constructor(public dialog: MatDialog, private _AppAdminService: AppAdminService, private toastyService: ToastaService) {
  }

  //
  ngOnInit(): void {
    this.cols = [
      {field: 'id', header: 'ID'},
      {field: 'name', header: 'Name'},
      {field: 'display_name', header: 'Display'},
      {field: 'description', header: 'Description'}
    ];
    this.getPermissionApp();
  }

  showDialog(data: any) {
    this.rolesPermations = data.permissions;
    console.log(data.permissions);
    this.display = true;
  }

  getPermissionApp() {
    this._AppAdminService.getPermission().subscribe((res: { data }) => {
      this.Permission = res.data as IAppAdmin[];
      // this.rolesPermations = this.roles.map(x => x.permissions);
      // console.log(this.rolesPermations);
    });
  }

  openRoleDeleteDialog(id) {
    const dialogRef = this.dialog.open(DeletePermationComponent, {
      data: {id}
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        // this.toastyService.success(this.toastOption);
        console.log(result);
        // this.eventsList.id = result;
        this.onDeleteMember(result);

      }
    });
  }

  onDeleteMember(roleId) {
    this._AppAdminService.deletePermission(roleId).subscribe(res => {
    }, error => {
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
    }, () => {
      this.getPermissionApp();
      this.toastyService.success({
        title: 'Members',
        msg: 'This Role Has Been Deleted successfully!',
        showClose: true,
        timeout: 3000,
        theme: 'material'
      });
      // this.displayClassDeleteDialogue = false;
      // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
    });
  }

}
